<template>
  <div id="app">
  <head>
    <meta property="og:url"           content="https://caionakai.github.io" />
    <meta property="og:type"          content="website" />
    <meta property="og:title"         content="Math Fun" />
    <meta property="og:description"   content="oi" />
    <meta property="og:image"         content="https://vignette.wikia.nocookie.net/naruto/images/0/04/Sarada_despertando_o_Sharingan.png/revision/latest?cb=20170818042748&path-prefix=pt-br" />
  </head>
    <!-- this div is needed to define full screen background color -->
    <div id="content">
      <h1 id="title">Math Fun</h1>
      <div id="panelButton">
        <a id="buttonJogar" href="#/map" class="button">
            <span>Jogar</span>
        </a>
        <a href="#/topics" class="button">
            <span>Aprender</span>
        </a>
        <a href="#/howtoplay" class="button">
            <span>Como jogar</span>
        </a>
      </div>
      <div id="fb-root"></div>
      <p>Compartilhe no facebook</p>
      <div class="fb-share-button" 
           data-href="https://caionakai.github.io" 
           data-layout="button"
           data-size="large"
      >
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Intro',
  created() {
    this.eventUpdate();
    this.resetAllLevels();
    this.restoreLife();
  },
  mounted(){
    this.face();
  },
  methods:{ 
    eventUpdate(){
      this.$emit('resetSomador');
    },
    resetAllLevels(){
      this.$emit('resetAllLevels');
    },
    restoreLife(){
      this.$emit('restoreLife');
    },
    face: function(d, s, id) {
    var js, fjs = d.getElementsByTagName(s)[0];
    if (d.getElementById(id)) return;
    js = d.createElement(s); js.id = id;
    js.src = "https://connect.facebook.net/en_US/sdk.js#xfbml=1&version=v3.0";
    fjs.parentNode.insertBefore(js, fjs);
  }(document, 'script', 'facebook-jssdk')
  }
}

</script>

<style scoped>
#buttonJogar{
  margin-left: 12%;
}
#content {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    background-position: 50% 50%;
    background-color: aquamarine;
}
#panelButton{
  margin: auto;
  width: 30%;
  height: 50%;
  border: 3px solid blue;
  border-radius: 10%;
  padding: 10px;
  margin-top: 0%;
}


#title{
  font-family: "Comic Sans MS", cursive, sans-serif;
}

.button {
  display: inline-block;
  margin-top: 10%;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  border-radius: 8px;
  -webkit-box-shadow: 0 8px 0 #4286f4, 0 15px 20px rgba(0, 0, 0, .35);
  -moz-box-shadow: 0 8px 0 #4286f4, 0 15px 20px rgba(0, 0, 0, .35);
  box-shadow: 0 8px 0 #4286f4, 0 15px 20px rgba(0, 0, 0, .35);
  -webkit-transition: -webkit-box-shadow .1s ease-in-out;
  -moz-transition: -moz-box-shadow .1s ease-in-out;
  -o-transition: -o-box-shadow .1s ease-in-out;
  transition: box-shadow .1s ease-in-out;
  font-size: 25px;
  color: #fff;
}
.button span {
  display: inline-block;
  padding: 10px 20px;
  background-color: #4286f4;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  border-radius: 8px;
  -webkit-box-shadow: inset 0 -1px 1px rgba(255, 255, 255, .15);
  -moz-box-shadow: inset 0 -1px 1px rgba(255, 255, 255, .15);
  box-shadow: inset 0 -1px 1px rgba(255, 255, 255, .15);
  line-height: 1;
  text-shadow: 0 -1px 1px rgba(175, 49, 95, .7);
  -webkit-transition: background-color .2s ease-in-out, -webkit-transform .1s ease-in-out;
  -moz-transition: background-color .2s ease-in-out, -moz-transform .1s ease-in-out;
  -o-transition: background-color .2s ease-in-out, -o-transform .1s ease-in-out;
  transition: background-color .2s ease-in-out, transform .1s ease-in-out;
  font-family: "Comic Sans MS", cursive, sans-serif;
}

.button:hover span {
  background-color: #4286f4;
  text-shadow: 0 -1px 1px rgba(175, 49, 95, .9), 0 0 5px rgba(255, 255, 255, .8);
}
.button:active, #button:focus {
  -webkit-box-shadow:    0 8px 0 #4286f4, 0 12px 10px rgba(0, 0, 0, .3);
  -moz-box-shadow: 0 8px 0 #4286f4, 0 12px 10px rgba(0, 0, 0, .3);
  box-shadow:    0 8px 0 #4286f4, 0 12px 10px rgba(0, 0, 0, .3);
}

.button:active span {
  -webkit-transform: translate(0, 4px);
  -moz-transform: translate(0, 4px);
  -o-transform: translate(0, 4px);
  transform: translate(0, 4px);
}

</style>



